        top 50px
        left 60px
        background url("./img/arrow-icon.png") no-repeat
        cursor pointer
      .tab
        margin 40px 0 10px 0
        ul
          display flex
          justify-content center
          li
            position relative
            width 120px
            height 50px
            line-height 50px
            font-size 24px
            color #1d7ebf
            font-weight 700
            text-align center
            cursor pointer
            &.active
              color #fff
              &:after
                content ''
                position absolute
                bottom -6px
                left 0
                width 100%
                height 6px
                border-radius 5px
                background-image linear-gradient(to right, #ffaa31, #41c0fb)
      .container
        position relative
        left -10px
        width 90%
        height 520px
        margin 0 auto
        overflow hidden
        .inner
          position relative
          .ul-letter
            .li-letter
              .letter
                color #36edf7
                font-size 16px
                line-height 20px
                margin-top 10px
                padding-left 20px
              .ul-name
                display flex
                justify-content start
                flex-wrap wrap
                .li-name
                  width 140px
                  height 32px
                  margin 10px 20px
                  padding 0 10px
                  line-height 32px
                  text-align center
                  font-size 16px
                  border-radius 20px
                  border 1px solid #36edf7
                  color #cbfbff
                  white-space nowrap
                  overflow hidden
                  text-overflow ellipsis
                  cursor pointer
                  &.active
                    color #013c3f
                    border 1px solid transparent
                    font-weight 700
                    background-image linear-gradient(to right, #ffaa31, #41c0fb)
        .inner-c
          display: flex;
          justify-content: start;
          flex-wrap: wrap;
          .ul-letter
            flex 1
            .li-name
              width 140px
              height 32px
              margin 10px 20px
              padding 0 10px
              line-height 32px
              text-align center
              font-size 16px
              border-radius 20px
              border 1px solid #36edf7
              color #cbfbff
              white-space nowrap
              overflow hidden
              text-overflow ellipsis
              cursor pointer
              &.active
                color #013c3f
                border 1px solid transparent
                font-weight 700
                background-image linear-gradient(to right, #ffaa31, #41c0fb)
        .inner-r
          .ul-letter
            flex 1
            .li-letter
              .cityColor
                width 140px
                height 32px
                margin 10px 10px 10px 0px
                padding 0 10px
                line-height 32px
                text-align left
                font-size 16px
                // border-radius 20px
                // border 1px solid #36edf7
                color #cbfbff
                white-space nowrap
                overflow hidden
                text-overflow ellipsis
                cursor pointer
                &.active
                  color #013c3f
                  border 1px solid transparent
                  font-weight 700
                  background-image linear-gradient(to right, #ffaa31, #41c0fb)4
              .ul-name
                flex 1
                display flex
                flex-direction row
                flex-wrap wrap
                .li-name
                  width 140px
                  height 32px
                  margin 10px 10px
                  padding 0 10px
                  line-height 32px
                  text-align center
                  font-size 16px
                  border-radius 20px
                  border 1px solid #36edf7
                  color #cbfbff
                  white-space nowrap
                  overflow hidden
                  text-overflow ellipsis
                  cursor pointer
                  &.active
                    color #013c3f
                    border 1px solid transparent
                    font-weight 700
                    background-image linear-gradient(to right, #ffaa31, #41c0fb)

      
      .btn
        width 160px
        height 44px
        margin 20px auto
        line-height 44px
        text-align center
        background url("./img/btn-bg.png") no-repeat
        color #cbfbff
        font-size 18px
        cursor pointer
</style>
